
   
   #gitdetails{
       display: flex;
       width: 98%;
       height: 250px;
       margin: auto;
     
   }
    #gitdetails>div{
 
   width: 49%;
   height: 99%;
   margin: auto;
   } 
    #gitdetails>div>img{
   width: 100%;
   height: 100%;
   margin-top: 10px;
    } 
    #gitdetlanguage{
    width: 40%;
    height: 250px;
    margin: auto;
    margin-top: 20px;
   } 
    #gitdetlanguage>img{
    width: 100%;
    height: 90%;
   } 


    @media all and (min-width: 768px) and (max-width: 1024px) {
      #gitdetails{
        /* border: 1px solid red; */
        display: flex;
        flex-direction: column;
        width: 90%;
        height: 250px;
        margin: auto;
        }
        #gitdetlanguage{
       
          /* border: 1px solid red; */
          
          margin-top: 30px;
         }
      } 

			/* small screens */
      @media all and (min-width: 320px) and (max-width: 767px) {
        
        .project-heading{
            margin: auto;
        }
        #gitdetails{
          /* border: 1px solid red; */
          display: flex;
          flex-direction: column;
          width: 98%;
          height: 150px;
          margin: auto;
          }
          #gitdetlanguage{
         
            /* border: 1px solid red; */
            height: 150px;
            margin-top: 30px;
           }
      }
